import { Button, Checkbox, CircularProgress, Grid } from '@mui/material';
import RequestsApi from 'api/requests';
import { concat, isEmpty, last, map } from 'lodash';
import { useEffect, useState } from 'react';

const OrderCheckBoxList = (props) => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);

  const addNumber = (check_name, check_alias) => {
    const counts = {};
    concat(map(props?.orderDetail?.subRows, 'check'), check_name)?.map(
      (step) => {
        if (!counts[step]) {
          counts[step] = 0;
        }
        counts[step]++;
        return `${check_alias}-${counts[step]}`;
      }
    );
    return `${check_alias ? check_alias : check_name}-${counts[check_name]}`;
  };

  useEffect(() => {
    setLoading(true);
    RequestsApi.ListChecksByClientPackage(
      props?.orderDetail?.client_id,
      props?.orderDetail?.checks_package
    )
      .then((response) => {
        let resp = JSON.parse(response.data.data);

        const updatedChecksValues = resp.map((item) => ({
          category: item.category,
          check_name: item.check_name,
          isAdded: false,
          qty: 0,
        }));

        setData(updatedChecksValues);
      })
      .catch((error) => {
        console.error('Error fetching data:', error);
      })
      .finally(() => setLoading(false));
  }, [props?.orderDetail]);

  const handleCheckboxChange = (check_name, check_alias) => {
    const isAlreadySelected = props?.selectedChecks?.some(
      (item) => item.check_name === check_name
    );

    let updatedSelectedChecks;
    if (isAlreadySelected) {
      updatedSelectedChecks = props?.selectedChecks.filter(
        (item) => item.check_name !== check_name
      );
    } else {
      updatedSelectedChecks = [
        ...props?.selectedChecks,
        {
          order_id: props?.orderDetail?.order_id,
          check_name: check_name,
          package_included: true,
          client_id: props?.orderDetail?.client_id,
          client_name: props?.orderDetail?.client_name,
          check_alias: addNumber(check_name, check_alias),
        },
      ];
    }

    console.log('updatedSelectedChecks', updatedSelectedChecks);

    props?.setSelectedChecks(updatedSelectedChecks);
  };

  console.log('props?.setSelectedChecks', props?.setSelectedChecks);
  return (
    <>
      {loading ? (
        <div style={{ display: 'flex', justifyContent: 'center' }}>
          <CircularProgress color='inherit' size={20} />
        </div>
      ) : (
        <>
          {map(data, (checkDetail, index) => {
            return (
              <div key={index}>
                <Checkbox
                  name='category'
                  value={checkDetail.check_name}
                  checked={
                    !isEmpty(props?.selectedChecks)
                      ? props?.selectedChecks?.some(
                          (item) => item?.check_name === checkDetail?.check_name
                        )
                      : false
                  }
                  onChange={() =>
                    handleCheckboxChange(checkDetail?.check_name, '')
                  }
                  variant='outlined'
                />
                <span>{checkDetail?.check_name}</span>
              </div>
            );
          })}
          <Grid item xs={12} sx={{ display: 'flex', justifyContent: 'end' }}>
            <Button
              variant='contained'
              color='primary'
              sx={{ marginRight: '0.7rem' }}
              onClick={props?.handleClose}
            >
              Cancel
            </Button>
            <Button
              variant='contained'
              color='primary'
              disabled={isEmpty(props?.selectedChecks)}
              type='submit'
              onClick={props?.handleNextClick}
            >
              Add
            </Button>
          </Grid>
        </>
      )}
    </>
  );
};

export default OrderCheckBoxList;
