import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import RemoveIcon from '@mui/icons-material/Remove';
import { LuMinusSquare, LuPlusSquare } from 'react-icons/lu';
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Box,
  Button,
  CircularProgress,
  Divider,
  Unstable_Grid2 as Grid,
  MenuItem,
  Modal,
  Paper,
  Select,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableRow,
  Typography,
} from '@mui/material';
import { styled } from '@mui/material/styles';
import RequestsApi from 'api/requests';
import {
  filter,
  find,
  includes,
  isEmpty,
  isObject,
  map,
  negate,
  size,
  uniqBy,
} from 'lodash';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import ReactLinkify from 'react-linkify';
import { useNavigate, useParams } from 'react-router-dom';
import {
  Roles,
  checkStatus,
  clientRoles,
  deposition,
  toastMessage,
} from 'views/Utils/Common';
import OrderCheckBoxList from './OrderCheckBoxList';
import { DownloadButton } from './pdfGenerator/DownloadPDF';
import { CancellationPopup } from '../MasterData/University/CancellationPopup';
import { FaEdit, FaRegEdit } from 'react-icons/fa';

// Assets
export default function OrderDetails() {
  const [order, setOrder] = useState([]);
  const [subRowList, setSubRowList] = useState([]);
  const [loading, setLoading] = useState(false);
  const [openCheckBoxModal, setOPenCheckBoxModal] = useState(false);
  const [selectedChecks, setSelectedChecks] = useState([]);
  const [cancelOrder, setCancelOrder] = useState({
    showModal: false,
    checksId: [],
  });
  const userDetail = localStorage.getItem('user');
  const role = localStorage?.getItem('role');
  let navigate = useNavigate();
  const { id } = useParams();
  const [expanded, setExpanded] = React.useState(false);

  const handleExpand = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  let token =
    'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJlbWFpbCI6ImhpbmFAc3RvcmUuY29tIiwiZXhwIjoxNjk1MTM2MzE0fQ.OexNPaFplvQdlZ1sydHYjUm1tujDtVWmC7048ue4b9M';

  const InputStyle = {
    fontsize: '0.75rem',
    padding: '8px 12px',
  };

  const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
    ...theme.typography.body2,
    // padding: theme.spacing(1),
    textAlign: 'center',
    color: theme?.palette?.background?.paper,
    borderRadius: '0.5rem',
  }));

  const getOrderDetail = async () => {
    setLoading(true);
    try {
      let response = await RequestsApi.OrderDetails({
        token: token,
        id: id,
      });

      let json = await response;
      let data = JSON.parse(json['data']['data'])['rowsData'];
      setOrder(data[0]);
      data.forEach((d) => {
        d['subRows'].forEach((s) => {
          setSubRowList((oldArray) =>
            uniqBy([...oldArray, s], 'order_checks_id')
          );
        });
      });
    } catch (error) {
      console.error('Error fetching checks:', error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (id) {
      getOrderDetail();
    }
  }, []);

  const handleDownload = () => {
    const pdfUrl = process.env.PUBLIC_URL + '/special-notice.pdf';
    const link = document.createElement('a');
    link.href = pdfUrl;
    link.download = 'report.pdf';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const handleClose = () => {
    setOPenCheckBoxModal(false);
    setSelectedChecks('');
  };

  const handleNextClick = () => {
    try {
      let response = RequestsApi.AddOrderchecks(selectedChecks);
      response?.then((res) => {
        if (res?.status === 200) {
          const res1 = RequestsApi.updateOrder(
            {
              status: 'DRAFT',
            },
            id
          );
          res1
            ?.then((response) => {
              if (response?.status === 200) {
                toastMessage('Check added successfully');
                handleClose();
                getOrderDetail();
              }
            })
            .catch((error) => {
              console.error('Error updating record:', error);
            });
        }
      });
    } catch (error) {
      console.error('Error fetching checks:', error);
    }
  };

  const handleChangeDeposition = (id) => (event) => {
    let data = {
      table_name: 'orders',
      field_to_update: 'score',
      new_value: event.target.value,
      table_id: id,
    };

    try {
      let response = RequestsApi.UpdateData(data);
      response?.then((res) => {
        if (res?.status === 200) {
          getOrderDetail();
        }
      });
    } catch (error) {
      console.error('Error fetching checks:', error);
    }
  };

  const handleDialogClose = () => {
    setCancelOrder({
      showModal: false,
      checksId: [],
    });
  };

  // const changeStatus = (status, checksId) => {
  //   let data = {
  //     table_name: 'orders',
  //     field_to_update: 'status',
  //     new_value: status,
  //     table_id: id,
  //   };
  //   let checkData = {
  //     table_name: 'order_checks',
  //     field_to_update: 'check_status',
  //     new_value: 'ARCHIVED',
  //     table_id: checksId,
  //     column_to_match: 'id',
  //   };
  //   try {
  //     let response = RequestsApi.UpdateData(data);
  //     response?.then((res) => {
  //       if (res.status === 200) {
  //         const response1 = RequestsApi.UpdateMultipleAssignee(checkData);
  //         response1?.then((res) => {
  //           if (res.status === 200) {
  //             const orderStatus = status === 'ARCHIVED' ? 'Archived' : 'Closed';
  //             toastMessage(`${orderStatus} order successfully`);
  //             navigate('/order/orders');
  //           }
  //         });
  //       }
  //     });
  //   } catch (error) {
  //     console.error('Error fetching checks:', error);
  //   }
  // };

  const cancellOrder = (orderData) => {
    let checkData = {
      table_name: 'order_checks',
      field_to_update: 'check_status',
      new_value: 'CANCELLED',
      table_id: cancelOrder?.checksId,
      column_to_match: 'id',
    };

    let response = RequestsApi.updateOrder(orderData, id);
    if (orderData?.status === 'CANCELLED') {
      response?.then((res) => {
        if (res?.status === 200) {
          RequestsApi.UpdateMultipleAssignee(checkData)?.then((res) => {
            if (res?.status === 200) {
              RequestsApi.UpdateMultipleAssignee({
                ...checkData,
                field_to_update: 'check_cancel_reason',
                new_value: orderData?.order_cancel_reason,
              }).then((res) => {
                if (res?.status === 200) {
                  toastMessage(
                    role === Roles?.ADMIN
                      ? 'Approval for cancel order send succesfully'
                      : 'Order cancelled succesfully'
                  );
                  handleDialogClose();
                }
              });
            }
          });
        }
      });
    } else {
      response?.then((res) => {
        if (res?.status === 200) {
          toastMessage(
            role === Roles?.ADMIN
              ? 'Approval for cancel order send succesfully'
              : 'Order cancelled succesfully'
          );
          handleDialogClose();
        }
      });
    }
  };

  const evenItems = subRowList.filter((_, i) => i % 2 === 0);
  const oddItems = subRowList.filter((_, i) => i % 2 !== 0);
  const shouldAddSpecialNoticeToOdd = oddItems.length < evenItems.length;

  return (
    <Stack
      spacing={1}
      divider={<Divider orientation='vertical' flexItem />}
      style={{ ...InputStyle }}
    >
      <Grid xs={6} md={12} sx={{ borderRadius: '0.5rem' }}>
        <span
          style={{
            fontWeight: 600,
            // textDecoration: 'underline',
            display: 'flex',
            justifyContent: 'center',
            fontSize: '1.1rem',
            textTransform: 'uppercase',
          }}
        >
          Order Details
        </span>
      </Grid>
      {loading ? (
        <div style={{ display: 'flex', justifyContent: 'center' }}>
          <CircularProgress color='inherit' size={20} />
        </div>
      ) : (
        <>
          <Box
            sx={{
              display: 'flex',
              justifyContent: 'space-between',
              width: '100%',
              mx: 2.5,
              pl: '1rem',
            }}
          >
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1 }}>
              <div style={{ display: 'flex', alignItems: 'center', gap: 15 }}>
                <Typography
                  sx={{ fontSize: '1.3rem !important', fontWeight: 450 }}
                >
                  {`${order?.first_name} ${order?.last_name}`}
                </Typography>

                <li style={{ color: 'grey', fontSize: '0.875rem' }}>
                  <span style={{ color: 'black' }}>{order?.client_name}</span>
                </li>
                <span>
                  <li style={{ color: 'grey', fontSize: '0.875rem' }}>
                    <span style={{ color: 'black' }}>
                      {order?.checks_package}
                    </span>
                  </li>
                </span>
              </div>
              <Typography
                sx={{
                  fontSize: '0.875rem !important',
                  display: 'flex',
                  gap: 2,
                }}
              >
                <span
                  style={{
                    border: '1px solid grey',
                    borderRadius: '0.3rem',
                    padding: '0px 7px',
                    color: find(checkStatus, {
                      label: order?.status,
                    })?.color,
                    borderColor: find(checkStatus, {
                      label: order?.status,
                    })?.color,
                  }}
                >
                  {order?.status}
                </span>
                <span>
                  <li style={{ color: 'grey' }}>
                    <span style={{ color: 'black' }}>
                      Order Created By - {order?.assignee}
                    </span>
                  </li>
                </span>
                <span>
                  <li style={{ color: 'grey' }}>
                    <span style={{ color: 'black' }}>
                      Order - {order?.order_id}
                    </span>
                  </li>
                </span>
                <span>
                  <li style={{ color: 'grey' }}>
                    <span style={{ color: 'black' }}>
                      {moment(order?.date)?.format('DD-MM-YYYY')}
                    </span>
                  </li>
                </span>
              </Typography>
            </Box>
            <Box>
              <div
                style={{
                  display: 'flex',
                  justifyContent: 'flex-end',
                  width: '100%',
                  gap: 4,
                  paddingRight: 11,
                }}
              >
                {/* Conditional Select Deposition */}
                {/* {order?.status === 'CLOSED' && (
                  <Select
                    id='score'
                    name='score'
                    value={order?.score}
                    onChange={handleChangeDeposition(id)}
                    disabled={includes(clientRoles, role)}
                    sx={{
                      marginTop: '0.5rem',
                      borderRadius: '20px',
                      backgroundColor: '#FFF',
                      '& .MuiOutlinedInput-root': {
                        borderRadius: '20px',

                        '&:hover': { borderColor: '#bf1e2e' },
                      },
                      '& .MuiOutlinedInput-notchedOutline': {
                        borderColor: '#e37d86',
                      },
                      '& .MuiSelect-select': {
                        padding: '7.5px 14px',
                        color: find(deposition, { label: order?.score })?.color,
                        textAlign: 'center', // Center align selected value
                      },
                    }}
                  >
                    {deposition.map((sts, index) => (
                      <MenuItem
                        value={sts?.label}
                        key={index}
                        sx={{ color: sts?.color }}
                      >
                        {sts.label}
                      </MenuItem>
                    ))}
                  </Select>
                )} */}
                {/* Edit/Fill Forms Button */}

                {order?.status !== 'CLOSED' && (
                  <Button
                    variant='contained'
                    color='primary'
                    sx={{
                      color: '#FFF',
                      textTransform: 'none',
                      borderRadius: '5px',
                      border: 'none',
                    }}
                    onClick={() => navigate(`/order/createorders/${id}`)}
                  >
                    Fill/Edit Forms
                  </Button>
                )}

                {/* Add Check/Component Button */}
                {order?.status !== 'CLOSED' && (
                  <Button
                    variant='contained'
                    color='primary'
                    sx={{
                      color: '#FFF',
                      textTransform: 'none',
                      borderRadius: '5px',
                      border: 'none',
                    }}
                    onClick={() => setOPenCheckBoxModal(true)}
                  >
                    {`Add ${
                      includes(clientRoles, role) ? 'Component' : 'Check'
                    }`}
                  </Button>
                )}

                {order?.status === 'CLOSED' && (
                  <Select
                    value={order?.score || 0}
                    onChange={handleChangeDeposition(id)}
                    disabled={includes(clientRoles, role)}
                    sx={{
                      minWidth: 120,
                      backgroundColor: 'primary',
                      p: '3.5px 15px',
                      '& .MuiOutlinedInput-input': {
                        p: '8.5px 14px !important',
                        color: '#FFF',
                      },
                      '& .MuiSelect-icon': {
                        color: '#FFF',
                      },
                    }}
                  >
                    <MenuItem value={0} disabled>
                      Select Desposition
                    </MenuItem>
                    {deposition?.map((sts, i) => (
                      <MenuItem
                        value={sts?.label}
                        key={i}
                        sx={{ color: sts?.color }}
                      >
                        {sts?.label}
                      </MenuItem>
                    ))}
                  </Select>
                  // <Select
                  //   id='score'
                  //   name='score'
                  //   value={order?.score}
                  //   onChange={handleChangeDeposition(id)}
                  //   disabled={includes(clientRoles, role)}
                  //   sx={{
                  //     marginTop: '0.5rem',
                  //     borderRadius: '20px',
                  //     backgroundColor: '#FFF',
                  //     width: '50%',
                  //     '& .MuiOutlinedInput-root': {
                  //       borderRadius: '20px',

                  //       '&:hover': { borderColor: '#bf1e2e' },
                  //     },
                  //     '& .MuiOutlinedInput-notchedOutline': {
                  //       borderColor: '#e37d86',
                  //     },
                  //     '& .MuiSelect-select': {
                  //       padding: '7.5px 14px',
                  //       color: find(deposition, { label: order?.score })?.color,
                  //       textAlign: 'center', // Center align selected value
                  //     },
                  //   }}
                  // >
                  //   {deposition.map((sts, index) => (
                  //     <MenuItem
                  //       value={sts?.label}
                  //       key={index}
                  //       sx={{ color: sts?.color }}
                  //     >
                  //       {sts.label}
                  //     </MenuItem>
                  //   ))}
                  // </Select>
                )}

                {order?.status === 'CLOSED' && (
                  // <Button
                  //   variant='contained'
                  //   color='primary'
                  //   sx={{
                  //     color: '#FFF',
                  //     textTransform: 'none',
                  //     borderRadius: '5px',
                  //     backgroundColor: '#bf1e2e',
                  //     border: 'none',
                  //   }}
                  //   onClick={() => {
                  //     window.open(
                  //       `/downloadPdf/${id}?token=${
                  //         JSON?.parse(userDetail)?.token
                  //       }`,
                  //       '_blank'
                  //     );
                  //   }}
                  // >
                  //   Download PDF
                  // </Button>
                  <DownloadButton
                    orderDetail={order}
                    fileName={`Order_${id}.pdf`}
                    id={id}
                  />
                )}
                {/* Conditional Cancel Order Button */}
                {role !== Roles?.VENDOR && role !== Roles?.STAFF && (
                  <Button
                    variant='contained'
                    color='primary'
                    sx={{
                      color: '#FFF',
                      textTransform: 'none',
                      borderRadius: '5px',
                      border: 'none',
                    }}
                    onClick={() =>
                      setCancelOrder({
                        showModal: true,
                        checksId: map(subRowList, 'order_checks_id'),
                      })
                    }
                  >
                    Cancel Order
                  </Button>
                )}
              </div>
            </Box>
          </Box>
          <Grid container spacing={2}>
            {subRowList?.map((x, i) => (
              <Grid
                item
                xs={12}
                sm={6}
                md={4}
                key={i}
                sx={{ display: 'flex', flexDirection: 'column', gap: 0.8 }}
              >
                {generateAccordionContent(
                  x,
                  i,
                  handleExpand,
                  expanded,
                  checkStatus,
                  order,
                  role,
                  navigate,
                  Item,
                  setExpanded
                )}
              </Grid>
            ))}
          </Grid>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <div style={{ fontSize: '0.9rem', marginBottom: 10, fontWeight: 600, paddingLeft:6,  }}>
                Special Notice
              </div>
              <SpecialNoticeAccordion
                handleExpand={handleExpand}
                expanded={expanded}
                handleDownload={handleDownload}
                subRowList={subRowList}
                Item={Item}
                setExpanded={setExpanded}
              />
            </Grid>
          </Grid>
        </>
      )}
      <Modal
        open={openCheckBoxModal}
        onClose={handleClose}
        aria-labelledby='modal-modal-title'
        aria-describedby='modal-modal-description'
      >
        <Box
          sx={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            width: 700,
            maxHeight: '80vh', // Adjust this value as needed
            bgcolor: 'background.paper',
            border: '2px solid #000',
            boxShadow: 24,
            p: 4,
            overflowY: 'auto', // Enable vertical scrolling within the modal if needed
            '& label': {
              lineHeight: '2em !important',
            },
          }}
        >
          <OrderCheckBoxList
            selectedChecks={selectedChecks || []}
            setSelectedChecks={setSelectedChecks}
            handleClose={handleClose}
            handleNextClick={handleNextClick}
            orderDetail={order}
            setOPenCheckBoxModal={setOPenCheckBoxModal}
          />
        </Box>
      </Modal>
      {cancelOrder?.showModal && (
        <CancellationPopup
          open={cancelOrder?.showModal}
          handleDialogClose={handleDialogClose}
          handleSubmit={cancellOrder}
          object={{
            ...order,
            order_cancel_reason: order?.order_cancel_reason
              ? JSON.parse(order?.order_cancel_reason)
              : '',
          }}
          cancel='order'
        />
        // <ConfirmationDialog
        //   open={cancelOrder?.showModal}
        //   handleDialogClose={handleDialogClose}
        //   handleChangeAssignee={() =>
        //     changeStatus(cancelOrder?.status, cancelOrder?.checksId)
        //   }
        // />
      )}
    </Stack>
  );
}

const generateAccordionContent = (
  x,
  i,
  handleExpand,
  expanded,
  checkStatus,
  order,
  role,
  navigate,
  Item,
  setExpanded
) => {
  const cleanedString = x?.checksformdata?.replace(/\\/g, '');
  const jsonObject = cleanedString ? JSON?.parse(cleanedString) : {};
  const entries = Object.entries(jsonObject?.AutoGenInfo || {});

  return (
    <Item key={i}>
      <Accordion
        onChange={handleExpand(i)}
        expanded={expanded === i}
        sx={{
          '& .MuiAccordionSummary-content.Mui-expanded': {
            minHeight: '47px !important',
          },
        }}
      >
        <AccordionSummary
          sx={{
            background: '#8e8f91',
            borderRadius: '0.5rem',
            mx: 0,
            minHeight: '50px !important',
            '& .MuiAccordionSummary-content.Mui-expanded': {
              margin: '0 !important',
            },
          }}
          expandIcon={
            expanded === i ? (
              <LuMinusSquare
                onClick={() => setExpanded(false)}
                alt=''
                size={18}
                color='#fff'
                style={{ marginLeft: '0.2rem', marginBottom: '0.1rem' }}
              />
            ) : (
              <LuPlusSquare
                onClick={() => setExpanded(true)}
                alt=''
                size={18}
                color='#fff'
                style={{ marginLeft: '0.2rem', marginBottom: '0.1rem' }}
              />
            )
          }
          aria-controls='panel1a-content'
          id='panel1a-header'
        >
          <Box
            sx={{
              display: 'flex',
              justifyContent: 'space-between',
              width: '100%',
              alignItems: 'center',
            }}
          >
            <div style={{ display: 'flex', gap: 12 }}>
              <span
                style={{ color: '#fff', fontWeight: 500, fontSize: '0.875rem' }}
              >
                {x?.check_alias || x?.check}
              </span>
              <Button
                style={{
                  background:
                    find(checkStatus, {
                      label: x?.status,
                    })?.color || '#BE1E2E',
                  color: '#fff',
                  padding: '0px 8px 0px 8px',
                }}
              >
                {x.status}
              </Button>
            </div>
            {order?.date_enabled &&
              role !== Roles?.STAFF &&
              role !== Roles?.VENDOR && (
                <span
                  onClick={() =>
                    navigate(`/orderchecks/checkDetails/${x?.order_checks_id}`)
                  }
                >
                  <FaEdit
                    size={18}
                    color='#fff'
                    style={{ marginTop: '0.2rem' }}
                  />
                </span>
              )}
          </Box>
        </AccordionSummary>
        <AccordionDetails sx={{ p: 0 }}>
          {!isEmpty(jsonObject?.AutoGenInfo) ? (
            <Table width='100%' border='0' sx={{ mt: '3px' }}>
              <TableBody>
                <TableRow sx={{ borderBottom: 'none' }}>
                  <TableCell sx={{ borderBottom: 'none', p: 0 }}>
                    <Table>
                      <TableBody>
                        {entries?.map(([label, value], index) => (
                          <TableRow key={label} sx={{ borderBottom: 'none' }}>
                            <TableCell
                              component='th'
                              scope='row'
                              sx={{
                                p: '10px',
                                width: '30%',
                                verticalAlign: 'middle',
                                borderBottom: 'none',
                              }}
                            >
                              <Typography
                                variant='subtitle1'
                                sx={{ fontWeight: 500 }}
                              >
                                {`${label}:`}
                              </Typography>
                            </TableCell>
                            <TableCell
                              sx={{
                                p: '10px',
                                verticalAlign: 'middle',
                                borderBottom: 'none',
                              }}
                            >
                              {value ? (
                                !isObject(value) ? (
                                  <ReactLinkify
                                    componentDecorator={(
                                      decoratedHref,
                                      decoratedText,
                                      key
                                    ) => (
                                      <a
                                        href={decoratedHref}
                                        key={key}
                                        target='_blank'
                                        rel='noopener noreferrer'
                                      >
                                        {decoratedText}
                                      </a>
                                    )}
                                  >
                                    {value}
                                  </ReactLinkify>
                                ) : (
                                  'NA'
                                )
                              ) : (
                                '-'
                              )}
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          ) : (
            <div
              style={{
                display: 'flex',
                justifyContent: 'center',
              }}
            >
              No data found
            </div>
          )}
        </AccordionDetails>
      </Accordion>
    </Item>
  );
};

const SpecialNoticeAccordion = ({
  handleExpand,
  expanded,
  handleDownload,
  subRowList,
  Item,
  setExpanded,
}) => (
  <Item key='report'>
    <Accordion
      onChange={handleExpand(size(subRowList) + 1)}
      expanded={expanded === size(subRowList) + 1}
    >
      <AccordionSummary
        sx={{
          background: '#8e8f91',
          borderRadius: '0.5rem',
          mx: 0,
          minHeight: '50px !important',
          '& .MuiAccordionSummary-content.Mui-expanded': {
            margin: '0 !important',
          },
        }}
        expandIcon={
          expanded === size(subRowList) + 1 ? (
            <LuMinusSquare
              onClick={() => setExpanded(false)}
              alt=''
              size={18}
              color='#fff'
              style={{ marginLeft: '0.2rem', marginBottom: '0.1rem' }}
            />
          ) : (
            <LuPlusSquare
              onClick={() => setExpanded(true)}
              alt=''
              size={18}
              color='#fff'
              style={{ marginLeft: '0.2rem', marginBottom: '0.1rem' }}
            />
          )
        }
        aria-controls='panel1a-content'
        id='panel1a-header'
      >
        <span style={{ color: '#fff', fontWeight: 500, fontSize: '0.875rem' }}>
          Special Notice
        </span>
      </AccordionSummary>
      <AccordionDetails>
        <Typography
          sx={{ display: 'flex', justifyContent: 'center', paddingTop: '1rem' }}
        >
          ** NOTICE **
        </Typography>
        <div
          style={{
            paddingTop: '1.5rem',
            display: 'flex',
            flexDirection: 'column',
            gap: 40,
          }}
        >
          <span>
            The information provided is a consumer report as defined in the
            federal Fair Credit Reporting Act [15 U.S.C. 1681-1681u]. It
            contains confidential information on the individual named. It is
            submitted to the conditions contained in your Subscriber Agreement
            with us and may be used solely as a factor in evaluating the named
            individual for property renting/leasing, employment, promotion,
            reassignment or retention as an employee. We maintain strict
            procedures designed to ensure that the information is complete and
            up to date. While the information furnished is from reliable
            sources, its accuracy is not guaranteed. Proper use of this report
            and final verification of the named individual's identity is your
            sole responsibility. If any adverse action is taken based in whole
            or in part on this consumer report, a copy of this report and a
            summary of the consumer's rights must be provided to the consumer
            prior to taking adverse action.
          </span>
          <span>
            <span
              onClick={handleDownload}
              style={{
                color: '#698FB5',
                background: '#fff',
                border: 'none!important',
                fontWeight: 500,
                fontSize: '0.875rem',
                cursor: 'pointer',
              }}
            >
              A Summary of Your Rights Under the Fair Credit Reporting Act
            </span>
          </span>
        </div>
      </AccordionDetails>
    </Accordion>
  </Item>
);
